/***/ (function(module, exports) {

	module.exports = {"data":{"post":{"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2005/04/squeezebox.gif\" border=\"0\" height=\"129\" width=\"200\" alt=\"squeezebox.gif\" align=\"right\" />This is my latest enhancement for my home workspace... a <a href=\"http://slimdevices.com/\">Squeezebox</a> wireless digital music player for mp3s and Internet radio for our existing stereo, controlled via remote or a web browser.</p>\n<p>The files stored on my <a href=\"http://www.papascott.de/archives/2005/01/18/linux-mini/\">NSLU2</a> running <a href=\"http://www.nslu2-linux.org/wiki/Unslung/HomePage\">Unslung</a>. The <a href=\"http://www.slimdevices.com/su_downloads.html\">SlimServer</a> (open-source, in Perl) is probably too hefty to run on Unslung; I've got it on a FreeBSD box which mounts the NSLU2 via NFS. The SlimServer can stream to software players as well as the Squeezebox and can serve several devices simultaneously. It seems much easier than having iTunes libraries on each PC. </p>\n<p>I was made aware of the Squeezebox by <a href=\"http://www.tbray.org/ongoing/When/200x/2005/03/12/USB-DAC\">a post by Tim Bray</a> a few weeks ago. Unlike him, we're no audiophiles... our existing setup is a 15-year-old mini stereo, but we can move the Squeezebox to whatever system we may upgrade to. It's not cheap at $300 (plus postage and customs), but seems to be much more convenient than the stacks of CDs we have accumulated. My initial impression is very positive, and even Mama the Technophobe agrees that it might be a nice thing to have.</p>\n<p>One warning... the Slim Devices order page said the unit would be shipped in 5 to 7 days. That must be a typo, they must mean hours instead of days. My order was shipped the same day, and arrived from California within 48 hours of my order.</p>","fields":{"title":"Welcome to Squeezebox","date":"22 Apr 2005","url":"https://papascott.de/2005-04-22-welcome-to-squeezebox/"}}},"pathContext":{"slug":"/2005-04-22-welcome-to-squeezebox/","prev":{"fields":{"title":"Understatement","date":"22 Apr 2005","slug":"/2005-04-22-understatement/"}},"next":{"fields":{"title":"airport penguins","date":"23 Apr 2005","slug":"/2005-04-23-airport-penguins/"}}}}

/***/ })

